body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  background-color: #5D737E;
  color: #25282b;
  font-weight: 400;
}

form{
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  background-color: #fff;
  color: #000;
  margin: 3% auto;
  border-radius: 15px;
}

.active-link{
  color: #E3B505;
}

form > .input-field {
  width: 90%;
}

.card{
  border-radius: 15px;
}

button{
  margin: 10%;
}
.btn, .btn-large{
  border-radius: 10px;
}
.error-red {
  background-color: #c24846;
  color: #8b0300;
}
  
.logo {
    font-size: 5vw;
}

.link-wrapper {
    flex: 2 1;
}

@media (min-width:1000px){
    .logo {
        font-size: 3em;
    }
}
@media (max-width:500px){
    .link-wrapper {
        padding:0
    }
    .link-wrapper > a {
        font-size: .8em;
    }
}
.spinner {
    width: 10vw;
    height: 10vw;
  
    position: relative;
    margin: 100px auto;
  }
  
  .double-bounce1, .double-bounce2 {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: #E3B505;
    opacity: 0.6;
    position: absolute;
    top: 0;
    left: 0;
    
    -webkit-animation: sk-bounce 2.0s infinite ease-in-out;
    animation: sk-bounce 2.0s infinite ease-in-out;
  }
  
  .double-bounce2 {
    -webkit-animation-delay: -1.0s;
    animation-delay: -1.0s;
  }
  
  @-webkit-keyframes sk-bounce {
    0%, 100% { -webkit-transform: scale(0.0) }
    50% { -webkit-transform: scale(1.0) }
  }
  
  @keyframes sk-bounce {
    0%, 100% { 
      transform: scale(0.0);
      -webkit-transform: scale(0.0);
    } 50% { 
      transform: scale(1.0);
      -webkit-transform: scale(1.0);
    }
  }
.modalWrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.contentWrapper{
    background-color: #FCFFFD;
    max-width: 70%;
    min-height: 20%;
    max-height: 70%;
    min-width: 25%;
    overflow-y: inherit;
    font-weight: 530;
    font-size: 2em;
    padding: 2%;
    text-align: center;
    border-radius: 15px;
    outline: none;
}

@media (max-width:600px){
    .contentWrapper {
        font-size: 1.em;
    }
}
